import { login, logout, getInfo } from '../../api/user';
import { getToken, setToken, removeToken } from '../../utils/auth';
import router, { resetRouter } from '../../router';

const state = {
    token: getToken(),
    name: '',
    avatar: '',
    introduction: '',
    roles: []
};

const mutations = {
    SET_TOKEN: (state, token) => {
        state.token = token;
    },
    SET_INTRODUCTION: (state, introduction) => {
        state.introduction = introduction;
    },
    SET_NAME: (state, name) => {
        state.name = name;
    },
    SET_AVATAR: (state, avatar) => {
        state.avatar = avatar;
    },
    SET_ROLES: (state, roles) => {
        state.roles = roles;
    }
};

const actions = {
    // user login
    login({ commit }, userInfo) {
        const { username, password } = userInfo;
        return new Promise((resolve, reject) => {
            login({ username: username.trim(), password: password }).then(response => {
                const { obj, code, msg } = response;
                if (code == 200) {
                    commit('SET_TOKEN', obj.CurToken);
                    commit('SET_NAME', obj.UserName);
                    commit('SET_AVATAR', obj.HeadImgurl);
                    console.log(obj);
                    localStorage.setItem('token', obj.CurToken);
                    console.log(obj.CurToken);
                    setToken(obj.CurToken);
                    resolve();
                } else {
                    reject(msg);
                }
            }).catch(error => {
                reject(error);
            });
        });
    },

    // get user info
    getInfo({ commit, state }) {
        return new Promise((resolve, reject) => {
            getInfo(state.token).then(response => {
                const { data } = response;

                if (!data) {
                    reject('Verification failed, please Login again.');
                }

                const { roles, name, avatar, introduction } = data;

                // roles must be a non-empty array
                if (!roles || roles.length <= 0) {
                    reject('getInfo: roles must be a non-null array!');
                }

                commit('SET_ROLES', roles);
                commit('SET_NAME', name);
                commit('SET_AVATAR', avatar);
                commit('SET_INTRODUCTION', introduction);
                resolve(data);
            }).catch(error => {
                reject(error);
            });
        });
    },

    // user logout
    logout({ commit, state, dispatch }) {
        return new Promise((resolve, reject) => {
            console.log('aaaaaaa');
            logout(state.token).then(() => {
                commit('SET_TOKEN', '');
                removeToken();
                resetRouter();

                // reset visited views and cached views
                // to fixed https://github.com/PanJiaChen/vue-element-admin/issues/2485
                dispatch('tagsView/delAllViews', null, { root: true });

                resolve();
            }).catch(error => {
                reject(error);
            });
        });
    },

    // remove token
    resetToken({ commit }) {
        return new Promise(resolve => {
            commit('SET_TOKEN', '');
            commit('SET_ROLES', []);
            removeToken();
            resolve();
        });
    },

    // dynamically modify permissions
    changeRoles({ commit, dispatch }, role) {
        return new Promise(async resolve => {
            const token = role + '-token';

            commit('SET_TOKEN', token);
            setToken(token);

            const { roles } = await dispatch('getInfo');

            resetRouter();

            // generate accessible routes map based on roles
            const accessRoutes = await dispatch('permission/generateRoutes', roles, { root: true });

            // dynamically add accessible routes
            router.addRoutes(accessRoutes);

            // reset visited views and cached views
            dispatch('tagsView/delAllViews', null, { root: true });

            resolve();
        });
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions
};
